.display-inline-block
{
  display:inline-block;
}
.spacer
{
    height:20px;
}



.section-part
{
    position:relative;
    margin-bottom:20px;
    padding:10px 20px;
    border:1px solid #CCC;
    background-color:#FFF;

    &.border-warning
    {
        .option-container
        {
            opacity:1 !important;
            color:#F00;
            &.active{
                color:inherit;
            }
        }
        background-color:rgba(255,2555,255,.5);
    }
    &.extra .dashicons-before:before
    {
        font-size:14px;
    }
}

.wrap.page-width
{
    max-width:760px;
}

.modules{
    position: relative;
    margin:0;
    display:flex;
    flex-wrap: wrap;
    .card{
        display:block;
        max-width:440px;
        min-height:240px;
        margin:0 15px 15px 0;
        @media only screen and (max-width: 500px) {
            margin:15px 0;
        }
    }
}



.sections-container .option-container
    {
    opacity:.5;
    &:hover,
    &.active{
        opacity:1;
    }



    display:block;
    padding-top:5px;
    padding-bottom:5px;
    position: relative;
    padding-left:30px;
    input[type=checkbox]{
        position: absolute;
        top:20px;
        left:0px;
        @media only screen and (max-width: 782px) {
            left:-7px;
        }
    
    }
}

.sections-container .text-container
{
    display: block;

    input[type=text],
    input[type=email]
    {
        display: block;
        width:100%;
        margin:5px 0;
    }
}

.nav-tab 
{
    cursor:pointer;

    .dashicons-before:before
    {
        position: relative;
        top:2px;
    }

    .dashicons-admin-plugins.active
    {
        opacity:1;
        color:#46b450;
    }

    .dashicons-admin-plugins.inactive
    {
        opacity:1;
        color: rgb(189, 0, 63);;
    }
}



/*SWITCH */
.switch {
    position: relative;
    display: block;
    float:right;
    right:-17px;
    width: 40px;
    height: 17px;
    cursor: default;
  }
  
  .switch input {display:none;}
  
  .slider {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.2);
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 13px;
    width: 13px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    -webkit-transition: .2s;
    transition: .2s;
  }
  .slider.active {
    background-color: #76d450;
  }
  .slider.changing {
    background-color: #0073aa;
  }

  .slider.active:before {
    -webkit-transform: translateX(23px);
    -ms-transform: translateX(23px);
    transform: translateX(23px);
  }
  
  /*------ ADDED CSS ---------*/
  .on, .off
  {
    color: white;
    position: absolute;
    top:0;
    left: 5px;
    width:30px;
    font-size: 8px;
    font-family: Verdana, sans-serif;
    text-align:right;
    user-select: none;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none;
  }
  .on
  {
    text-align:left;
    display: none;
  }

  .active .off{ display:none;}
  .active .on{ display:block;}

  /*--------- END --------*/
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 17px;
    width:40px;
    height:17px;
  }
  
  .slider.round:before {
    border-radius: 50%;}


